import React from 'react';
import axios from 'axios';
import Movie from './Movie.js';
import "./App.css";

// state를 쓸거면 class 컴포넌트를 이용
class App extends React.Component{
  
  state={
    isLoading: true,
    movies: [],
  }
  
  // await를 쓰기 위해 async를 함수에 붙혀준다.
  getMovies= async ()=>{
    // axios는 객체로 값 리턴
    const {
      data:{
        data:{
          movies
        }
      }
    } = await axios.get("https://yts.mx/api/v2/list_movies.json?sort=rating");
    this.setState({movies: movies, isLoading: false});
  }
  componentDidMount(){
    this.getMovies();
  }
  
  render(){
    const {isLoading, movies} = this.state;
    return (
      <section className="container">
          { isLoading 
            ? <div className="loader">
                <span className="loader__text">Loading...</span>
              </div> 
            :(
              <div className="movies">
                {movies.map(movie=>(
                <Movie 
                  key={movie.id} 
                  title={movie.title} 
                  year={movie.year} 
                  summary={movie.summary} 
                  genres={movie.genres}
                  poster={movie.medium_cover_image}
                />
              ))}
              </div>
            )
          }
      </section>
    );
  }      
}

export default App;
